  content: "";
  outline: 1px grey dotted;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

@keyframes spinme {
  0% {
    transform: perspective(28rem) rotateY(0deg);
  }
  2% {
    transform: perspective(28rem) rotateY(5deg);
  }
  4% {
    transform: perspective(28rem) rotateY(-5deg);
  }
  8% {
    transform: perspective(28rem) rotateY(5deg);
  }
  16% {
    transform: perspective(28rem) rotateY(-5deg);
  }
  32% {
    transform: perspective(28rem) rotateY(0deg);
  }
}
