  font-size: 16px !important;
}

.select-color {
  position: relative;
}
.select-color:after {
  content: "";
  position: absolute;
  width: 0;
  height: 0;
  right: 1.4rem;
  top: 1.3rem;
  border-left: 6px solid transparent;
  border-right: 6px solid transparent;
  border-top: 6px solid black;
  opacity: 1;
  transition: all 0.3s;
}

.select-color.is-Open:after {
  opacity: 0;
}

.is-Closed .select-color-pill {
  transition: max-height 0.3s, opacity 0.3s;
  transition-delay: 0s, 0.3s;
}
.is-Closed .select-color-pill::before {
  border-bottom-width: 0px;
}

.is-Open .select-color-pill {
  transition: opacity 0.3s, max-height 0.3s;
  transition-delay: 0s, 0.3s;
}

.select-color-pill {
  display: flex;
  flex-wrap: nowrap;
  overflow: hidden;
  align-items: center;
  cursor: default;
  max-height: 3rem;
  opacity: 1;
  position: relative;
}
.select-color-pill.pill-hidden {
  max-height: 0;
  opacity: 0;
}
.select-color-pill::before {
  content: "";
  width: 100%;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  height: 0;
  border-bottom: 1px dashed lightgray;
}
.select-color-pill:last-child::before {
  border-bottom-width: 0px;
}
