/*! Snipcart variables. Starting Snipcart 3.2.X you can define colors in variables */
/*! Snipcart Theming: https://docs.snipcart.com/v3/setup/theming */
/* Syntax to integrate SASS variables into CSS variables: https://sass-lang.com/documentation/breaking-changes/css-vars */
#snipcart {
  --color-default: #212B36;
  --bgColor-default: white;
  --borderColor-default: #e3e6e8;
  --color-buttonPrimary: #FFFFFF;
  --color-buttonPrimary-hover: #FFFFFF;
  --bgColor-buttonPrimary: #007CC2;
  --bgColor-buttonPrimary-hover: #00649c;
  --borderColor-buttonPrimary: #007CC2;
  --borderColor-buttonPrimary-hover: #00649c;
  --shadow-buttonPrimary-hover: 0 2px 4px 0 rgba(0, 124, 194, 0.2);
  --color-buttonSecondary: #007CC2;
  --color-buttonSecondary-hover: #00649c;
  --bgColor-buttonSecondary: #FFFFFF;
  --bgColor-buttonSecondary-hover: #FFFFFF;
  --borderColor-buttonSecondary: #007CC2;
  --borderColor-buttonSecondary-hover: #00649c;
  --shadow-buttonSecondary-hover: 0 2px 4px 0 rgba(0, 124, 194, 0.2);
  --shadow-default: 0 0.25rem 0.25rem 0 rgba(0, 124, 194, 0.1);
  --bgColor-input: #FFFFFF;
  --borderColor-input: #dceaf9;
  --borderColor-input-hover: #dceaf9;
  --borderColor-input-focus: #007CC2;
  --shadow-input-focus: 0 2px 4px 0 rgba(0, 124, 194, 0.2);
  --shadow-input-error: 0 2px 4px 0 rgba(241, 26, 34, 0.2);
  --color-icon: #212B36;
  --color-link: #007CC2;
  --color-link-hover: #00639b;
  --color-badge: #212B36;
  --bgColor-modal: transparent;
  --bgColor-modalVeil: rgba(0,0,0,0.4);
  --bgColor-alt: white;
}

/*
 PETR STEPANOV:
 CSS taken from: https://cdn.snipcart.com/themes/v3.6.1/default/snipcart.css

 Below we remove remove font loading
 @import "https://fonts.bunny.net/css?family=Montserrat:300,500|Open+Sans:300,600";

 Commment out checkmark SVG: checkmark.svg
*/
.snipcart {
  font-family: Open Sans, sans-serif;
  font-size: 14px;
  font-weight: 300;
  color: #2e3338;
  color: var(--color-default, #2e3338);
}

.snipcart * {
  margin: 0;
  padding: 0;
  border: 0;
  font: inherit;
  color: inherit;
  background: 0 0;
  display: block;
  line-height: 1;
  list-style: none;
  quotes: none;
  border-collapse: collapse;
  border-spacing: 0;
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.snipcart__font--secondary,
.snipcart__font--subtitle,
.snipcart__font--subtitle-small {
  font-family: Montserrat, sans-serif;
}

.snipcart__font--small {
  font-size: 10px;
}

.snipcart__font--subtitle-small,
.snipcart__font--tiny {
  font-size: 12px;
}

.snipcart__font--std {
  font-size: 14px;
  line-height: 20px;
}

.snipcart__font--large {
  font-size: 16px;
}

.snipcart__box--badge,
.snipcart__font--subtitle,
.snipcart__font--xlarge {
  font-size: 18px;
}

.snipcart__font--slim {
  font-weight: 300;
}

.snipcart__font--regular {
  font-weight: 400;
}

.snipcart__font--bold {
  font-weight: 500;
  line-height: 20px;
}

.snipcart__font--black,
.snipcart__font--subtitle,
.snipcart__font--subtitle-small {
  font-weight: 600;
}

.snipcart__font--subtitle {
  display: flex;
}

.snipcart__font--subtitle-small {
  display: flex;
  text-transform: uppercase;
}

.snipcart__font--blue {
  color: #1a4db3;
  color: var(--color-link, #1a4db3);
}

.snipcart__box {
  padding: 32px 16px;
  position: relative;
  margin-bottom: 10.66667px;
  background-color: #fff;
  background-color: var(--bgColor-default, #fff);
}

@media (min-width: 1024px) {
  .snipcart__box {
    padding: 32px;
    margin-top: 0;
  }
}
.snipcart__box--large {
  padding: 32px;
  margin-top: 0;
}

.snipcart__box--no-margin {
  margin-bottom: 0;
}

.snipcart__box--gray {
  background-color: #f7f7f8;
  background-color: var(--bgColor-alt, #f7f7f8);
}

.snipcart__box--slim {
  padding: 32px 32px 16px;
}

@media (min-width: 1024px) {
  .snipcart__box--slim {
    padding: 16px 32px;
  }
}
.snipcart__box--slim--large {
  padding: 16px 32px;
}

.snipcart__box--header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@media (min-width: 1024px) {
  .snipcart__box--title {
    display: flex;
    justify-content: flex-start;
    align-items: center;
  }
}
.snipcart__box--subtitle,
.snipcart__box--title--large {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.snipcart__box--content {
  padding: 10.66667px 0;
}

.snipcart__box--badge {
  height: 40px;
  width: 40px;
  border-radius: 999px;
  border: 3px solid;
  color: #0d59f2;
  color: var(--color-badge, #0d59f2);
  border-color: transparent;
  border-color: var(--borderColor-badge, transparent);
  background-color: #fff;
  background-color: var(--bgColor-badge, #fff);
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: -48px;
  margin-bottom: 10.66667px;
}

@media (min-width: 1024px) {
  .snipcart__box--badge {
    margin-top: 0;
    margin-bottom: 0;
    margin-left: -48px;
    margin-right: 8px;
  }
}
.snipcart__box--badge--large {
  margin-top: 0;
  margin-bottom: 0;
  margin-left: -48px;
  margin-right: 8px;
}

.snipcart__box--badge-disabled {
  color: #2e3338;
  color: var(--color-badge-disabled, #2e3338);
  border-color: #e3e6e8;
  border-color: var(--borderColor-badge-disabled, #e3e6e8);
  background-color: #f7f7f8;
  background-color: var(--bgColor-badge-disabled, #f7f7f8);
}

.snipcart__box--badge-highlight {
  color: #0d59f2;
  color: var(--color-badge-active, #0d59f2);
  border-color: transparent;
  border-color: var(--borderColor-badge-active, transparent);
  background-color: #d9f0fc;
  background-color: var(--bgColor-badge-active, #d9f0fc);
}

.snipcart-modal {
  position: relative;
  margin: 0 auto;
  max-width: 100%;
  background-color: #f1f2f4;
  background-color: var(--bgColor-modal, #f1f2f4);
}

@supports (-webkit-overflow-scrolling: touch) {
  .snipcart-modal input,
  .snipcart-modal select,
  .snipcart-modal textarea {
    font-size: 16px;
  }
}
.snipcart-modal__container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  min-height: 100%;
  overflow-y: auto;
  z-index: 20;
  background-color: #f1f2f4;
  background-color: var(--bgColor-modal, #f1f2f4);
